<template>
  <div class="header">
    <p><img src="../assets/logo.png" alt=""></p>
    <el-form v-if="id === '1'" ref="form" :model="form" label-position="left" label-width="100px" style="display: flex;">
      <el-form-item label="请选择平台：" class="approval-select">
        <el-select v-model="form.status" @change="search">
          <el-option v-for="item in systemIds" :key="item.value" :label="item.value" :value="item"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div v-else="id === '2'" style="color:#fff;">
      当前平台：{{systemId}}
    </div>
  </div>
</template>

<script>
import {search} from '../axios/api'
import Vue from 'vue'
export default {
  data () {
    return {
      form: {
        status: ''
      },
      systemIds: [],
      systemId: '',
      id: '1'
    }
  },
  created(){

  },
  mounted () {
    window.addEventListener('setItem', ()=> {
      this.systemId = localStorage.getItem('watchStorage');
      this.id = localStorage.getItem('id');
    })
    this.first();
  },
  methods: {
    first(){
      let that = this;
      search().then(function(res){
        if (res.status === 200) {
          that.systemIds = res.data.systemIds;
          that.form.status = that.systemIds[0];
          Vue.prototype.resetSetItem('watchStorage', that.systemIds[0]);
        } else {
          that.$message.error("查询错误，请稍后再试")
        }
      }).catch(function(err){
        console.log(err)
      })
    },
    search(){
      let that = this;
      search().then(function(res){
        if (res.status === 200) {
          that.systemIds = res.data.systemIds;
          Vue.prototype.resetSetItem('watchStorage', that.form.status);
        } else {
          that.$message.error("查询错误，请稍后再试")
        }
      }).catch(function(err){
        console.log(err)
      })
    },
  }
}
</script>

<style scoped lang="scss">
  .header {
    width: 100%;
    height: 70px;
    background: #151515;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    p {
      margin: 0;
      font-size: 24px;
      color: white;
      padding-left: 20px;
      img{
        width: 110px;
        height: 42px;
      }
    }
    .nav {
      display: flex;
      flex-direction: row;
      box-sizing: border-box;
      p {
        font-size: 16px;
        margin-right: 22px;
        &:nth-child(2) {
          cursor: pointer;
        }
      }
      img {
        width: 40px;
        height: 40px;
        position: relative;
        top: 16px;
        right: 5px;
        border-radius: 50%;
      }
    }
  }
  /deep/ .el-form-item{
    margin:0;
  }
  /deep/ .el-form-item__label{
    color:#fff;
  }
</style>



