<template>
  <div class="wrapper">
    <div class="dataStatistics-container">
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台当前总开立额度</h4>
          <p class="number">{{total_amount}}</p>
          <!--<p>-->
            <!--<span v-if="isFrozen === 0" class="status frozen">冻结</span>-->
            <!--<span v-if="isFrozen === 1" class="status notFrozen">未冻结</span>-->
            <!--<span class="amount">100,000,000</span>-->
          <!--</p>-->
        </div>
        <el-progress :percentage="percent1" status="warning" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台当前可用开立额度</h4>
          <p class="number">{{available_amount}}</p>
        </div>
        <el-progress :percentage="percent2" status="success" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台当前已用开立额度</h4>
          <p class="number">{{used_amount}}</p>
        </div>
        <el-progress :percentage="percent3" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>开立中开立额度总额</h4>
          <p class="number">{{opening_amount}}</p>
        </div>
        <el-progress :percentage="percent4" :show-text="false"></el-progress>
      </div>
      <!--<div class="data-box-container" v-if="isFrozen">-->
        <!--<div class="data-box">-->
          <!--<h4>冻结</h4>-->
          <!--<p class="number">100,000,000</p>-->
        <!--</div>-->
        <!--<el-progress :percentage="100" status="exception" :show-text="false"></el-progress>-->
      <!--</div>-->
    </div>
    <div class="list-container">
      <div class="search-box">
        <div class="name-box">
          <label>公司名称：</label>
          <el-input clearable v-model="name" @change="clearBtn"></el-input>
        </div>
        <div class="code-box">
          <label>F码(机构ID)：</label>
          <el-input clearable v-model="code" @change="clearBtn"></el-input>
        </div>
        <el-button class="search-btn" @click="doSearch">查询</el-button>
        <el-button class="reset-btn" @click="doReset" :disabled="isClickable">重置</el-button>
      </div>
      <el-row style="margin-top: 20px;box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);min-height:500px;">
        <el-table :data="tableData" stripe v-loading="loading" element-loading-text="拼命加载中">
          <el-table-column prop="ent_name" label="机构名称" align="center"></el-table-column>
          <el-table-column prop="ent_id" label="机构ID" align="center"></el-table-column>
          <el-table-column prop="init_amount" label="机构初始开立额度总额" align="center" class="table-header"></el-table-column>
          <el-table-column prop="available_amount" label="机构当前可用开立额度" align="center"></el-table-column>
          <el-table-column prop="start_date" label="当前开立额度起始日" align="center" class="table-header"></el-table-column>
          <el-table-column prop="end_date" label="当前开立额度到期日" align="center" class="table-header"></el-table-column>
          <el-table-column prop="used_amount" label="机构当前已用开立额度" align="center" class="table-header"></el-table-column>
          <el-table-column prop="opening_amount" label="开立中开立额度总额" align="center" class="table-header"></el-table-column>
          <el-table-column prop="children_lock_amount" label="独立子企业锁定" align="center" class="table-header"></el-table-column>
          <el-table-column prop="" label="操作" align="center">
            <template slot-scope="scope">
              <el-button @click="openDetail(scope.row)" type="text" size="small">查看开立记录</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div id="page" v-show="page.totalNum > 10" class="block" ref="pageBox">
          <el-pagination
            background
            :model="page"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page.pageIndex"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="page.pageSize"
            layout="prev, pager, next, sizes, total, jumper"
            :total="page.totalNum">
          </el-pagination>
        </div>
      </el-row>
    </div>
  </div>
</template>
<script>

  import {formatNum} from '../../util/validator'
  import {creditListProgress, creditList} from '../../axios/api'
  import Vue from 'vue'

  export default {
    data(){
      return {
        loading: false,
        percent1: 0,
        percent2: 0,
        percent3: 0,
        percent4: 0,
        total_amount: "",
        available_amount: "",
        used_amount: "",
        opening_amount: "",
        colors:[{color: "#FFAB2B"}],
        // isFrozen: true,
        name: '',
        code: '',
        isClickable: true,
        page: {
          pageIndex: 1,
          pageSize: 10,
          totalPage: 0,
          totalNum: 0
        },
        tableData: [],
        systemId: '',
      }
    },
    mounted(){
      // 禁止浏览器前进后退
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", function() {
        history.pushState(null, null, document.URL);
      });
      this.systemId = localStorage.getItem('watchStorage');
      Vue.prototype.resetSetItem('id', '1');
      this.progressData();
      window.addEventListener('setItem', this.getSystemId, false)
    },
    methods:{
      getSystemId(){
        this.systemId = localStorage.getItem('watchStorage');
        this.progressData();
      },
      //查询
      doSearch(){
        this.page.pageIndex = 1;
        this.search();
      },
      //重置
      doReset(){
        this.name = '';
        this.code = '';
        this.search();
      },
      //清除输入信息
      clearBtn(param) {
        if(param === '') {
          this.page.pageIndex = 1
          this.search()
        }
      },
      handleSizeChange(val) {
        this.page.pageSize = val;
        this.page.pageIndex = 1;
        this.search();
      },
      handleCurrentChange (val) {
        this.page.pageIndex = val;
        this.search();
      },
      getNum(val) {
        if (isNaN(val)) {
          return 0;
        }else{
          return val;
        }
      },
      //顶部数据
      progressData(){
        let that = this;
        let params = {
          systemId: that.systemId
        };
        creditListProgress(params).then(function(res){
          // console.log(res);
          if (res.status === 200) {
            that.total_amount = formatNum(res.data.total_amount);
            that.available_amount = formatNum(res.data.available_amount);
            that.used_amount = formatNum(res.data.used_amount);
            that.opening_amount = formatNum(res.data.opening_amount);
            if(parseFloat(res.data.total_amount) > 0){
              that.percent1 = 100;
            }else{
              that.percent2 = 0;
            }
            if(parseFloat(res.data.available_amount) > 0){
              that.percent2 = that.getNum(Math.round((parseFloat(res.data.available_amount)*10000)/(parseFloat(res.data.total_amount)*100)));
            }else{
              that.percent2 = 0;
            }
            // console.log('that.percent2====',that.percent2);
            that.percent3 = that.getNum(Math.round((parseFloat(res.data.used_amount)*10000)/(parseFloat(res.data.total_amount)*100)));
            // console.log('that.percent3====',that.percent3);
            that.percent4 = that.getNum(Math.round((parseFloat(res.data.opening_amount)*10000)/(parseFloat(res.data.total_amount)*100)));
            that.search();
          } else {
            that.$message.error("查询错误，请稍后再试")
          }
        }).catch(function(err){
          console.log(err)
        })
      },
      //列表数据
      search(){
        let that = this;
        let params = {
          page: that.page.pageIndex,
          per_page: that.page.pageSize,
          ent_name: that.name,
          ent_id: that.code,
          systemId: that.systemId
        };
        that.loading = true;
        creditList(params).then(function(res){
          // console.log(res);
          that.loading = false;
          if (res.status === 200) {
            that.page.totalNum = res.data.count;
            that.page.totalPage = res.data.total_pages;
            that.tableData = res.data.data;
          } else {
            that.$message.error(res.data.message)
          }
        }).catch(function(err){
          that.loading = false;
          console.log(err)
        })
      },
      openDetail(row){
        this.$router.push({path:'/CreditQuery/CreditQueryDetails', query:{ent_id:row.ent_id}})
      }
    },
    watch:{
      name: function(){
        this.page.pageIndex = 1;
        // this.page.pageSize = 10;
        if(this.name !== '' || this.code !== ''){
          this.isClickable = false;
        }else{
          this.isClickable = true;
        }
        // this.search();
      },
      code: function(){
        this.page.pageIndex = 1;
        // this.page.pageSize = 10;
        if(this.name !== '' || this.code !== ''){
          this.isClickable = false;
        }else{
          this.isClickable = true;
        }
        // this.search();
      }
    },
    beforeDestroy(){
      window.removeEventListener('setItem', this.getSystemId, false)
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper{
    width:100%;
    .dataStatistics-container{
      display: flex;
      width:96%;
      padding:50px 40px 30px;
      .data-box-container{
        flex:1;
        box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);
        margin-right:20px;
        .data-box{
          padding:0 14px;
          text-align: left;
          h4{
            color:#252631;
            position: relative;
            white-space: nowrap;
          }
          p{
            margin:20px 0;
            position: relative;
            height:22px;
            .status{
              width:48px;
              height:20px;
              line-height: 20px;
              border-radius: 3px;
              color:#fff;
              text-align: center;
              font-size: 12px;
              display: inline-block;
              vertical-align: middle;
            }
            .frozen{
              background: #C11A1A;
            }
            .notFrozen{
              background: #60B201;
            }
            .amount{
              color:#98A9BC;
              font-size: 22px;
              position: absolute;
              right:0;
            }
          }
          .number{
            color:#98A9BC;
            font-size: 21px;
            text-align: right;
          }
        }
      }
    }
    .list-container{
      border-top:1px solid #E1E1E1;
      padding:50px 40px 0;
      .search-box{
        display: flex;
        .name-box, .code-box{
          width:400px;
          margin-right:50px;
          display: flex;
          label{
            width:120px;
            height: 40px;
            line-height: 40px;
            font-size: 14px;
            color:#252631;
            text-align: left;
          }
        }
        .search-btn, .reset-btn{
          width:86px;
          height:38px;
          text-align: center;
          vertical-align: middle;
          font-size: 14px;
          color:#fff;
        }
        .search-btn{
          background: #4F7BA6;
        }
        .reset-btn{
          background: #C4C4C4;
        }
      }
    }
    /deep/ .el-button--text{
      color:#4F7BA6;
    }
  }
</style>



