<template>
  <div class="wrapper">
    <div class="bread">
      <el-breadcrumb class="el-breadcrumb_g" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/TongBaoQuery/TongBaoQueryList' }"><i class="fa fa-home" aria-hidden="true"></i> 通宝查询</el-breadcrumb-item>
        <el-breadcrumb-item><i class="fa fa-folder-open-o" aria-hidden="true"></i> 流转/融资交易流水</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search">
      <div class="select-box">
        <el-select placeholder="业务类型" v-model="searchData.type" clearable @change="searchList">
          <el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.label"></el-option>
        </el-select>
      </div>
    </div>
    <el-row style="margin-top: 25px;box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);min-height:500px;">
      <el-table :data="tableData" stripe v-loading="loading" element-loading-text="拼命加载中">
        <el-table-column prop="transfer_no" label="流转编号/融资流水号" align="center"></el-table-column>
        <el-table-column prop="transfer_type" label="业务类型" align="center"></el-table-column>
        <el-table-column prop="form_ent_id" label="转出方/融资方ID" align="center" class="table-header"></el-table-column>
        <el-table-column prop="hold_ent_id" label="受让方/金融机构ID" align="center"></el-table-column>
        <el-table-column prop="operator_id" label="转出方企业操作员ID" align="center" class="table-header"></el-table-column>
        <!--<el-table-column prop="inputEnterpriseID" label="受让方企业ID" align="center" class="table-header"></el-table-column>-->
        <el-table-column prop="amount" label="流转/融资总金额" align="center" class="table-header"></el-table-column>
        <el-table-column prop="transfer_time" label="交易日期" align="center" class="table-header"></el-table-column>
      </el-table>
      <div id="page" v-show="page.totalNum > 10" class="block" ref="pageBox">
        <el-pagination
          background
          :model="page"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageIndex"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="page.pageSize"
          layout="prev, pager, next, sizes, total, jumper"
          :total="page.totalNum">
        </el-pagination>
      </div>
    </el-row>
  </div>
</template>
<script>

  import {TBHistoryList} from '../../axios/api'
  import Vue from 'vue'

  export default{
    data(){
      return{
        loading: false,
        page: {
          pageIndex: 1,
          pageSize: 10,
          totalPage: 0,
          totalNum: 0
        },
        tableData:[],
        typeList: [
          {
            value: "1",
            label: "流转"
          },
          {
            value: "2",
            label: "融资"
          }
        ],
        searchData: {
          type: '',
        },
        tongbao_no: "",
        systemId: ''
      }
    },
    mounted(){
      // 禁止浏览器前进后退
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", function() {
        history.pushState(null, null, document.URL);
      });
      this.hold_no = this.$route.query.hold_no;
      this.systemId = localStorage.getItem('watchStorage');
      Vue.prototype.resetSetItem('id', '2');
      this.search();
    },
    methods:{
      handleSizeChange(val) {
        this.page.pageSize = val;
        this.page.pageIndex = 1;
        this.search();
      },
      handleCurrentChange (val) {
        this.page.pageIndex = val;
        this.search();
      },
      //列表数据
      search(){
        let that = this;
        let params = {
          page: that.page.pageIndex,
          per_page: that.page.pageSize,
          hold_no: that.hold_no,
          transfer_type: that.searchData.type,
          systemId: that.systemId
        };
        that.loading = true;
        TBHistoryList(params).then(function(res){
          that.loading = false;
          if (res.status === 200) {
            that.page.totalNum = res.data.count;
            that.page.totalPage = res.data.total_pages;
            that.tableData = res.data.data;
          } else {
            that.$message.error("查询错误，请稍后再试")
          }
        }).catch(function(err){
          that.loading = false;
          console.log(err)
        })
      },
      //切换下拉框更新数据
      searchList(){
        this.page.pageIndex = 1;
        this.search();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper{
    padding:30px 50px 0;
    .select-box{
      text-align: right;
    }
  }
</style>



