<template>
    <el-container :span="24" class="container">
        <el-header class="header" style="height:75px;">
            <Header/>
        </el-header>
        <el-container :span="24" class="main" :style="{minHeight: minHeight + 'px'}">
            <el-aside class="aside">
              <el-col :span="12">
                <el-menu router :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
                  <el-menu-item index="/CreditQuery/CreditQueryList" :class="isActive('/CreditQuery') ? 'is-active' : ''" style="padding-left:40px;margin-top:20px;">
                    <i class="fa fa-file-text-o"></i>
                    <span>开立额度查询</span>
                  </el-menu-item>
                  <el-menu-item index="/TongBaoQuery/TongBaoQueryList" :class="isActive('/TongBaoQuery') ? 'is-active' : ''" style="padding-left:40px;">
                    <i class="fa fa-dot-circle-o"></i>
                    <span>通宝查询</span>
                  </el-menu-item>
                </el-menu>
              </el-col>
            </el-aside>
            <el-main>
                <router-view/>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import Header from '@/components/Header.vue'

export default {
  components: {
    Header
  },
  data () {
    return {
      minHeight: 0
    }
  },
  mounted () {
    this.minHeight = document.documentElement.clientHeight - 60
    var that = this
    window.onresize = function () {
      that.minHeight = document.documentElement.clientHeight - 60
    }
    // 禁止浏览器前进后退
    history.pushState(null, null, document.URL);
    window.addEventListener("popstate", function() {
      history.pushState(null, null, document.URL);
    });
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    isActive (menu) {
      return this.$route.path.indexOf(menu) > -1
    },
  }
}
</script>
<style lang="scss" scoped>
    .container {
        position: absolute;
        min-width: 1200px;
        bottom: 0;
        width: 100%;
        top:0;
        .header {
            background: #151515;
        }
        .aside {
            background: #151515;
            width: 14% !important;
        }
        .main{
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            overflow-x: hidden;
            .el-main{
                overflow-y: auto;
                background: #ffffff;
                overflow-x: hidden;
            }
        }
        /deep/ .el-menu-item{
          span{
            margin-left:20px;
            font-size: 16px;
          }
          .fa{
            font-size: 16px;
          }
        }
    }
</style>



