<template>
  <div class="wrapper">
    <div class="bread">
      <el-breadcrumb class="el-breadcrumb_g" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/CreditQuery/CreditQueryList' }"><i class="fa fa-home" aria-hidden="true"></i> 开立额度查询</el-breadcrumb-item>
        <el-breadcrumb-item><i class="fa fa-folder-open-o" aria-hidden="true"></i> 开立额度使用记录（开立）</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search">
      <div class="select-box">
        <el-select placeholder="状态" v-model="searchData.status" clearable @change="searchList">
          <el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.label"></el-option>
        </el-select>
      </div>
    </div>
    <el-row style="margin-top: 25px;box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);min-height:500px;">
      <el-table :data="tableData" stripe v-loading="loading" element-loading-text="拼命加载中">
        <el-table-column prop="tongbao_id" label="通宝编号" align="center"></el-table-column>
        <el-table-column prop="apply_ent_id" label="开立企业ID" align="center"></el-table-column>
        <el-table-column prop="apply_ent_name" label="开立企业名称" align="center" class="table-header"></el-table-column>
        <el-table-column prop="apply_operator_id" label="开立企业操作人员ID" align="center"></el-table-column>
        <el-table-column prop="receive_ent_id" label="接收企业ID" align="center" class="table-header"></el-table-column>
        <el-table-column prop="receive_ent_name" label="接受企业名称" align="center" class="table-header"></el-table-column>
        <el-table-column prop="open_amount" label="开立金额" align="center" class="table-header"></el-table-column>
        <el-table-column prop="hold_id" label="通宝持有编号" align="center" class="table-header"></el-table-column>
        <el-table-column prop="redeem_time" label="兑付日期" align="center" class="table-header"></el-table-column>
        <el-table-column prop="status" label="状态" align="center"></el-table-column>
      </el-table>
      <div id="page" v-show="page.totalNum > 10" class="block" ref="pageBox">
        <el-pagination
          background
          :model="page"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageIndex"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="page.pageSize"
          layout="prev, pager, next, sizes, total, jumper"
          :total="page.totalNum">
        </el-pagination>
      </div>
    </el-row>
  </div>
</template>
<script>

  import {creditHistoryList} from '../../axios/api'
  import Vue from 'vue'

  export default{
    data(){
      return{
        loading: false,
        page: {
          pageIndex: 1,
          pageSize: 10,
          totalPage: 0,
          totalNum: 0
        },
        tableData: [],
        typeList: [
          {
            value: "1",
            label: "开立中"
          },
          {
            value: "2",
            label: "开立成功"
          },
          {
            value: "3",
            label: "开立失败"
          }
        ],
        searchData: {
          status: '',
        },
        ent_id: "",
        systemId: '',
        id: ''
      }
    },
    mounted(){
      // 禁止浏览器前进后退
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", function() {
        history.pushState(null, null, document.URL);
      });
      this.ent_id = this.$route.query.ent_id;
      this.systemId = localStorage.getItem('watchStorage');
      Vue.prototype.resetSetItem('id', '2');
      this.search();
    },
    methods:{
      handleSizeChange(val) {
        this.page.pageSize = val;
        this.page.pageIndex = 1;
        this.search();
      },
      handleCurrentChange (val) {
        this.page.pageIndex = val;
        this.search();
      },
      //列表数据
      search(){
        let that = this;
        let params = {
          page: that.page.pageIndex,
          per_page: that.page.pageSize,
          ent_id: that.ent_id,
          status: that.searchData.status,
          systemId: that.systemId
        };
        that.loading = true;
        creditHistoryList(params).then(function(res){
          // console.log(res);
          that.loading = false;
          if (res.status === 200) {
            that.page.totalNum = res.data.count;
            that.page.totalPage = res.data.total_pages;
            that.tableData = res.data.data;
          } else {
            that.$message.error("查询错误，请稍后再试")
          }
        }).catch(function(err){
          that.loading = false;
          console.log(err)
        })
      },
      //切换下拉框更新数据
      searchList(){
        this.page.pageIndex = 1;
        this.search();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper{
    padding:30px 50px 0;
    .select-box{
      text-align: right;
    }
  }
</style>



