import axios from 'axios'
import { Message } from 'element-ui';
export function fetch (options) {
  if (options.method === 'GET') {
    let url = options.url;
    if (String(url).indexOf('?') === -1) {
      options.url = `${options.url}?ts=${Math.random()}`
    } else {
      options.url = `${options.url}&ts=${Math.random()}`
    }
  }
  return new Promise((resolve, reject) => {
    const instance = axios.create({
      headers: {
        'Content-Type': 'application/json'
      },
      timeout: 180 * 1000 // 180秒超时
    });
    instance(options)
      .then(response => {
        resolve(response)
      }).catch(function (error) {
      if (error && error.response) {
        switch (error.response.status) {
          case 601:
            error.message = '登陆超时，请重新登录！';
            Message.error({
              showClose: true,
              duration:2000,
              message: error.message
            });
            setTimeout(function(){
              window.location.replace('/');
            }, 1500);
            break;
          case 602:
            error.message = '用户无权限';
            break;
          default:
            Message.closeAll();
            error.message = `连接错误，请稍后再试`
        }
      } else {
        error.message = "连接到服务器失败";
      }
      Message.error({
        showClose: true,
        duration:2000,
        message: error.message,
        type: 'error'
      });
      reject(error)
    })
  })
}



