<template>
  <div class="wrapper">
    <div class="dataStatistics-container">
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台当前开立总额</h4>
          <p class="number">{{total_amount}}</p>
        </div>
        <el-progress :percentage="percent1" status="warning" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台当前通宝总额</h4>
          <p class="number">{{available_amount}}</p>
        </div>
        <el-progress :percentage="percent3" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>当前锁定总额</h4>
          <p class="number">{{lock_amount}}</p>
        </div>
        <el-progress :percentage="percent4" :show-text="false"></el-progress>
      </div>
      <div class="data-box-container">
        <div class="data-box">
          <h4>平台已兑付通宝总额</h4>
          <p class="number">{{redeem_amount}}</p>
        </div>
        <!--<el-progress :percentage="percent2" status="success" :show-text="false"></el-progress>-->
      </div>
    </div>
    <div class="list-container">
      <div class="search-box">
        <div class="name-box">
          <label>持有机构名称：</label>
          <el-input clearable v-model="name" @change="clearBtn"></el-input>
        </div>
        <div class="code-box">
          <label>开立编号：</label>
          <el-input clearable v-model="openCode" @change="clearBtn"></el-input>
        </div>
        <div class="code-box">
          <label>持有编号：</label>
          <el-input clearable v-model="ownCode" @change="clearBtn"></el-input>
        </div>
        <el-button class="search-btn" @click="doSearch">查询</el-button>
        <el-button class="reset-btn" @click="doReset" :disabled="isClickable">重置</el-button>
      </div>
      <el-row style="margin-top: 20px;box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);min-height:500px;">
        <el-table :data="tableData" stripe v-loading="loading" element-loading-text="拼命加载中">
          <el-table-column prop="ent_name" label="持有机构" align="center"></el-table-column>
          <el-table-column prop="ent_id" label="持有机构ID" align="center"></el-table-column>
          <el-table-column prop="open_no" label="开立编号" align="center"></el-table-column>
          <el-table-column prop="hold_no" label="持有编号" align="center"></el-table-column>
          <el-table-column prop="init_amount" label="初始通宝总额" align="center" class="table-header"></el-table-column>
          <el-table-column prop="balance_amount" label="当前通宝余额" align="center"></el-table-column>
          <el-table-column prop="transfer_amount" label="转出额" align="center" class="table-header"></el-table-column>
          <el-table-column prop="redeem_amount" label="已兑付总额" align="center" class="table-header"></el-table-column>
          <el-table-column prop="lock_amount" label="锁定通宝" align="center" class="table-header"></el-table-column>
          <el-table-column prop="" label="操作" align="center">
            <template slot-scope="scope">
              <el-button @click="openDetail(scope.row)" type="text" size="small">查看交易流水</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div id="page" v-show="page.totalNum > 10" class="block" ref="pageBox">
          <el-pagination
            background
            :model="page"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page.pageIndex"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="page.pageSize"
            layout="prev, pager, next, sizes, total, jumper"
            :total="page.totalNum">
          </el-pagination>
        </div>
      </el-row>
    </div>
  </div>
</template>
<script>

  import {formatNum} from '../../util/validator'
  import {TBListProgress, TBList} from '../../axios/api'
  import Vue from 'vue'

  export default {
    data(){
      return {
        loading: false,
        percent1: 0,
        // percent2: 0,
        percent3: 0,
        percent4: 0,
        total_amount: "",
        redeem_amount: "",
        available_amount: "",
        lock_amount: "",
        name: '',
        openCode: '',
        ownCode: '',
        isClickable: true,
        page: {
          pageIndex: 1,
          pageSize: 10,
          totalPage: 0,
          totalNum: 0
        },
        tableData: [],
        systemId: ''
      }
    },
    mounted(){
      // 禁止浏览器前进后退
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", function() {
        history.pushState(null, null, document.URL);
      });
      this.systemId = localStorage.getItem('watchStorage');
      Vue.prototype.resetSetItem('id', '1');
      this.progressData();
      window.addEventListener('setItem', this.getSystemId, false)
    },
    methods:{
      getSystemId(){
        this.systemId = localStorage.getItem('watchStorage');
        this.progressData();
      },
      //查询
      doSearch(){
        this.page.pageIndex = 1;
        this.search();
      },
      //重置
      doReset(){
        this.name = '';
        this.openCode = '';
        this.ownCode = '';
        this.search();
      },
      //清除输入信息
      clearBtn(param) {
        if(param === '') {
          this.page.pageIndex = 1;
          this.search();
        }
      },
      handleSizeChange(val) {
        this.page.pageSize = val;
        this.page.pageIndex = 1;
        this.search();
      },
      handleCurrentChange (val) {
        this.page.pageIndex = val;
        this.search();
      },
      getNum(val) {
        if (isNaN(val)) {
          return 0;
        }else{
          return val;
        }
      },
      //顶部数据
      progressData(){
        let that = this;
        let params = {
          systemId: that.systemId
        };
        TBListProgress(params).then(function(res){
          // console.log(res);
          if (res.status === 200) {
            that.total_amount = formatNum(res.data.total_amount);
            that.redeem_amount = formatNum(res.data.redeem_amount);
            that.available_amount = formatNum(res.data.available_amount);
            that.lock_amount = formatNum(res.data.lock_amount);
            if(parseFloat(res.data.total_amount) > 0){
              that.percent1 = 100;
            }else{
              that.percent1 = 0;
            }
            // that.percent2 = that.getNum(Math.round((res.data.redeem_amount*10000)/(res.data.total_amount*100)));
            that.percent3 = that.getNum(Math.round((parseFloat(res.data.available_amount)*10000)/(parseFloat(res.data.total_amount)*100)));
            that.percent4 = that.getNum(Math.round((parseFloat(res.data.lock_amount)*10000)/(parseFloat(res.data.total_amount)*100)));
            // console.log('that.percent1====',that.percent1);
            // console.log('that.percent3====',that.percent3);
            // console.log('that.percent4====',that.percent4);
            that.search();
          } else {
            that.$message.error("查询错误，请稍后再试")
          }
        }).catch(function(err){
          console.log(err)
        })
      },
      //列表数据
      search(){
        let that = this;
        let params = {
          page: that.page.pageIndex,
          per_page: that.page.pageSize,
          ent_name: that.name,
          open_no: that.openCode,
          hold_no: that.ownCode,
          systemId: that.systemId
        };
        that.loading = true;
        TBList(params).then(function(res){
          // console.log(res);
          that.loading = false;
          if (res.status === 200) {
            that.page.totalNum = res.data.count;
            that.page.totalPage = res.data.total_pages;
            that.tableData = res.data.data;
          } else {
            that.$message.error("查询错误，请稍后再试")
          }
        }).catch(function(err){
          that.loading = false;
          console.log(err)
        })
      },
      openDetail(row){
        this.$router.push({path:'/TongBaoQuery/TongBaoQueryDetails', query:{hold_no:row.hold_no}});
      }
    },
    watch:{
      name: function(){
        this.page.pageIndex = 1;
        // this.page.pageSize = 10;
        if(this.name !== '' || this.ownCode !== ''){
          this.isClickable = false;
        }else{
          this.isClickable = true;
        }
        // this.search();
      },
      openCode: function(){
        this.page.pageIndex = 1;
        // this.page.pageSize = 10;
        if(this.name !== '' || this.openCode !== '' || this.ownCode !== ''){
          this.isClickable = false;
        }else{
          this.isClickable = true;
        }
        // this.search();
      },
      ownCode: function(){
        this.page.pageIndex = 1;
        // this.page.pageSize = 10;
        if(this.name !== '' || this.openCode !== '' || this.ownCode !== ''){
          this.isClickable = false;
        }else{
          this.isClickable = true;
        }
        // this.search();
      }
    },
    beforeDestroy(){
      window.removeEventListener('setItem', this.getSystemId, false)
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper{
    width:100%;
    .dataStatistics-container{
      display: flex;
      width:96%;
      margin:50px 40px 30px;
      .data-box-container{
        width:24%;
        box-shadow: 0 2px 12px 1px rgba(223,223,223,0.50);
        margin-right:20px;
        .data-box{
          padding:0 14px;
          text-align: left;
          h4{
            color:#252631;
            position: relative;
            white-space: nowrap;
          }
          p{
            margin:20px 0;
            position: relative;
            height:22px;
            .status{
              width:48px;
              height:20px;
              line-height: 20px;
              border-radius: 3px;
              color:#fff;
              text-align: center;
              font-size: 12px;
              display: inline-block;
              vertical-align: middle;
            }
            .frozen{
              background: #C11A1A;
            }
            .notFrozen{
              background: #60B201;
            }
            .amount{
              color:#98A9BC;
              font-size: 22px;
              position: absolute;
              right:0;
            }
          }
          .number{
            color:#98A9BC;
            font-size: 21px;
            text-align: right;
          }
        }
      }
    }
    .list-container{
      border-top:1px solid #E1E1E1;
      padding:50px 40px 0;
      .search-box{
        display: flex;
        .name-box, .code-box{
          width:400px;
          margin-right:50px;
          display: flex;
          label{
            width:140px;
            height: 40px;
            line-height: 40px;
            font-size: 14px;
            color:#252631;
            text-align: left;
          }
        }
        .search-btn, .reset-btn{
          width:86px;
          height:38px;
          text-align: center;
          vertical-align: middle;
          font-size: 14px;
          color:#fff;
        }
        .search-btn{
          background: #4F7BA6;
        }
        .reset-btn{
          background: #C4C4C4;
        }
      }
    }
    /deep/ .el-button--text{
      color:#4F7BA6;
    }
  }
</style>



