import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import Home from '@/views/Home'
import Login from '@/views/Login'
import CreditQueryList from '@/views/CreditQuery/CreditQueryList'
import CreditQueryDetails from '@/views/CreditQuery/CreditQueryDetails'
import TongBaoQueryList from '@/views/TongBaoQuery/TongBaoQueryList'
import TongBaoQueryDetails from '@/views/TongBaoQuery/TongBaoQueryDetails'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/',
      redirect: '/CreditQuery/CreditQueryList',
      component: Home,
      children: [
        // 授信查询及子页面
        {
          path: '/CreditQuery/CreditQueryList',
          name: 'CreditQueryList',
          component: CreditQueryList
        },
        {
          path: '/CreditQuery/CreditQueryDetails',
          name: 'CreditQueryDetails',
          component: CreditQueryDetails
        },
        // 通宝查询及子页面
        {
          path: '/TongBaoQuery/TongBaoQueryList',
          name: 'TongBaoQueryList',
          component: TongBaoQueryList
        },
        {
          path: '/TongBaoQuery/TongBaoQueryDetails',
          name: 'TongBaoQueryDetails',
          component: TongBaoQueryDetails
        }
      ]
    }
  ]
})



