import { fetch } from './fetch'
import api from './url'

//新增下拉框接口
export function search () {
  return fetch({
    url: api.search,
    method: "GET",
    params: {}
  })
}

//授信查询页面 顶部数据
export function creditListProgress (param) {
  return fetch({
    url: api.creditListPro,
    method: "GET",
    params: {
      systemId: param.systemId
    }
  })
}

//授信查询页面 列表数据
export function creditList (param) {
  return fetch({
    url: api.creditList,
    method: "GET",
    params: {
      page: param.page,
      per_page: param.per_page,
      ent_name: param.ent_name,
      ent_id: param.ent_id,
      systemId: param.systemId
    }
  })
}

//授信使用记录 列表及查询
export function creditHistoryList (param) {
  return fetch({
    url: api.creditHistoryList,
    method: "GET",
    params: {
      page: param.page,
      per_page: param.per_page,
      ent_id: param.ent_id,
      status: param.status,
      systemId: param.systemId
    }
  })
}

//通宝查询页面 顶部数据
export function TBListProgress (param) {
  return fetch({
    url: api.TBListPro,
    method: "GET",
    params: {
      systemId: param.systemId
    }
  })
}

//通宝查询页面 列表数据
export function TBList (param) {
  return fetch({
    url: api.TBList,
    method: "GET",
    params: {
      page: param.page,
      per_page: param.per_page,
      ent_name: param.ent_name,
      open_no: param.open_no,
      hold_no: param.hold_no,
      systemId: param.systemId
    }
  })
}

//流转/融资交易流水 列表及查询
export function TBHistoryList (param) {
  return fetch({
    url: api.TBHistoryList,
    method: "GET",
    params: {
      page: param.page,
      per_page: param.per_page,
      hold_no: param.hold_no,
      transfer_type: param.transfer_type,
      systemId: param.systemId
    }
  })
}



